<template lang="pug">
	.dashboard(v-bind:class="{ 'animate-dashboard': lastPartAnimation, 'user-company': selectedCampaign.kind == 'copy' }")
		.client-and-campaing-info
			.client
				span Client: 
				p {{ selectedCampaign.campaignClient }}

			.campaign
				span Campaign: 
				p {{ selectedCampaign.name }}

		.logo-and-cards
			.campaign-info(v-if="dataLoad")
				.first-card
					.date-card
						span.card-title End Date:
						p(v-if="selectedCampaign.kind == 'standard'") {{ campaignData.end_date.split('/')[0] }}
							span /
							| {{ campaignData.end_date.split('/')[1] }}
							span /
							| {{ campaignData.end_date.split('/')[2] }}

						p(v-else) --
							span /
							| --
							span /
							| ----

				.second-card
					.leads-stat
						.til
							p Remaining Leads / Lead Goal

						.from-to(v-if="selectedCampaign.kind == 'standard'")
							p {{ campaignData.generated }}
							p.blue.slash / 
							p.blue {{ campaignData.total_goal }}

						.from-to(v-else)
							p 0
							p.blue.slash / 
							p.blue 0

				.third-card
					.leads-stat
						.til
							p Leads Generated:

						.from-to(v-if="selectedCampaign.kind == 'standard'")
							p(v-if="campaignData.total_goal != 0 ") {{  Number(campaignData.generated / campaignData.total_goal * 100).toFixed(0) }}%
							p(v-else) 0%

						.from-to(v-else)
							p 0%

				.four-card
					.user-manager
						.photo(v-if="campaignData.managed_by.photo_url")
							img(
								:src="campaignData.managed_by.photo_url",
								alt=""
							)

						.photo.no-photo(v-else)

						.desc
							.by
								p Managed by

							.name
								p {{ campaignData.managed_by.name }}

			.main-logo(
				v-bind:class="{'anim-logo': logoAnim}"
			)

				img(src="@/assets/logo-dashboard.svg", alt="")
		
		.user-campaign
			h4 Your campaign is being launched...

		.hours-block
			.all-statistic(v-if="dataLoad && selectedCampaign.kind == 'standard'")
				.database
					.left-side
						p.lt Current DataBase
						p.lb INFUSEmedia

					.right-side
						p.rt 138+ million 

				.target
					.left-side
						p.lt Targeted contacts
						p.lb Engagement in Process

					.right-side
						p.rt 8+ million

				.verified
					.left-side
						span.link-verified Verified
						p.lb In Process of Validation

					.right-side
						p.rt 574
						p.rb 101

				.lead-goal-c
					.left-side
						span.lead-goal(v-on:click="showLeadPopup()") Lead Goal
						p.rb {{ campaignData.total_goal }}

				.leads-percent-block
					.leads-percent
						.single.top
							.candle
								.fill(v-bind:style="{ height: campaignData.generated_pos.top + '%'}")
							.info
								p.value Top
								.b-line
								p.percent {{ campaignData.generated_pos.top }}%

						.single.middle
							.candle
								.fill(v-bind:style="{ height: campaignData.generated_pos.middle + '%'}")
							.info
								p.value Middle
								.b-line
								p.percent {{ campaignData.generated_pos.middle }}%

						.single.bottom
							.candle
								.fill(v-bind:style="{ height: campaignData.generated_pos.bottom + '%'}")
							.info
								p.value Bottom
								.b-line
								p.percent {{ campaignData.generated_pos.bottom }}%

			.hours(
				v-bind:class="{'first-step-anim': firstAnim, 'second-step-anim': secondAnim}"
			)

				.top-part(v-bind:class="{'animate-top': topAnim}")
