<script>
	import IntegratePopup from '@/components/Integrate-Popup'
	import LeadGoal from '@/components/Lead-Goal'

	import { mapMutations } from 'vuex'
	export default {
		name: 'HourGlass',
		components: {
			IntegratePopup,
			LeadGoal
		},
		data(){
			return {
				hourMatrix: [],
				fullSpeed: 10000,
				logoAnim: false,
				firstAnim: false,
				secondAnim: false,
				topAnim: false,
				bottomAnim: false,
				animationFinished: false,
				selectedCampaign: Object,
				campaignData: Object,
				dataLoad: false,
				reverseAnimation: false,
				lastPartAnimation: false,
				// components
				showIntegrate: false,
				showGoal: false

			}
		},
		created: function(){
			// this.runHour();
			// window.SelectC.init();
			setTimeout(() => this.startFirstAnim(), 1000)
		},
		computed: {
			SC(){
				return this.$store.getters.SC.id;
			},
			// SelectedCampaign() {
			// 	return this.$store.getters.SelectedCampaign
			// },
			RightSidebar(){
				return this.$store.getters.RightSidebar;
			}
		},
		watch: {
			SC(){
				// this.RS = this.$store.state.SC;
				this.selectedCampaign = this.$store.state.SelectedCampaign;
				this.loadCampaignData();
			},
			// SelectedCampaign(){
			// 	this.selectedCampaign = this.$store.state.SelectedCampaign;
			// }
		},
		methods:{
			resetAnim(){
				// this.firstAnim = false
				// this.secondAnim = false
				// this.logoAnim = false
				// this.topAnim = false
				// this.bottomAnim = false
				// setTimeout(() => this.startFirstAnim(), 3000)
				this.lastPartAnimation = false;
				setTimeout(() => {
					this.lastPartAnimation = true;
				}, 3000);
			},
			startFirstAnim(){
				this.logoAnim = true;
				this.firstAnim = true;
				setTimeout(() => this.secondAnim = true, 1000)
				setTimeout(() => {
					this.topAnim = true;
					this.bottomAnim = true
				} , 2000);
				setTimeout(() => {
					this.animationFinished = true;
					this.loadCampaignData()
				}, 4000);
			},
			calculateLeads(){
				this.reverseAnimation = true;
				setTimeout(() => {
					this.lastPartAnimation = true;
				}, 500);
				setTimeout(() => {
					this.reverseAnimation = false;
				}, 1100);
			},
			...mapMutations(['setRS']),
			loadCampaignData(){
				// /campaign/1/hourglass/id
				if (this.animationFinished && this.selectedCampaign.id) {
					var id = this.selectedCampaign.id;
					window.api.get('campaign/'+id+'/hourglass')
						.then(campaign => {
							this.campaignData = campaign.data;
							this.calculateLeads();
							this.logoAnim = false;
							this.dataLoad = true;
							this.setRS({
								sections: this.campaignData.sections,
								tactics: this.campaignData.tactics
							});
						})
						.catch(campaignError => {
							console.log(campaignError);
						})
				}
			},
			showIntegratePopup(){
				this.showIntegrate = true;
			},
			showLeadPopup(){
				this.showGoal = true;
			},
			hidePopups(){
				this.showIntegrate = false;
				this.editGoal = false;
			}
			// runHour(){
			// 	setInterval(function(){
					
			// 	}, fullSpeed);
			// }
		}
	}
</script>