						.cl
							p(v-if="item.leads_generated") {{ item.leads_generated }}
							p(v-else) -

						.cl
							p(v-if="item.remaining_leads") {{ item.remaining_leads }}
							p(v-else) -
							//- p(v-if="item.leads_goal > item.leads_generated") {{ item.leads_goal - item.leads_generated }}

						.action
							.settings(v-bind:class='item.grade', v-on:click="editDataShow(index)")


					//- .t-row
