<template lang="pug">
	.right-sidebar
		UserBlock

		.actions(v-if="activeCampaign")
			button.btn.play-pause.paused(type="button") Pause
			button.btn.engine(type="button") Engine

		.scroll-list(v-if="activeCampaign")
			scrolly.scroll.foo
				scrolly-viewport
					.camaing-setup
						.title
							p.count
								span {{ RightSidebar.sections.filter(item => item.enabled == true).length }}
								| /{{RightSidebar.sections.length}}

							router-link(to="/settings", target="_blank").campaign-link Campaign Setup

						.setup-list
							//- .single(v-for="(item, index) in RightSidebar.sections", v-if="setupLimit == false || index < setupLimit")
							//- 	input(type="checkbox", :id="'setup-'+item.id", v-model="item.enabled").check
							//- 	label(:for="'setup-'+item.id") {{ item.name }}
							//- 	.sm-ico
							.single(v-for="(item, index) in RightSidebar.sections", v-if="setupLimit == false || index < setupLimit")
								input(type="checkbox", :id="'setup-'+item.id", v-model="item.enabled").check
								label(:for="'setup-'+item.id")
								router-link(:to="'/settings#'+item.slug", target="_blank") {{ item.name }}
								.sm-ico

							.more-list
								.show-more(v-on:click="showMore()", v-bind:class="{active: setupLimit == false}")

					.tactics
						.title
							p.count
								span {{ RightSidebar.tactics.filter(item => item.active == true).length }}
								| /{{RightSidebar.tactics.length}}

							router-link(to="/settings", target="_blank").tactics-activated Tactics Activated

						.tactics-list
							.single-link(v-for="item in RightSidebar.tactics")
								router-link(:to="'/settings'+item.id", v-bind:class="{active: item.active}").link-to {{ item.name }}

				.scroll-bar
				scrolly-bar(axis="y")
		
		.static-bar(v-if="!activeCampaign")
			.database(v-bind:class="{'start-anim': DatabaseAnim}")
				p Database of
				b 138+
					span million
				p Decision Makers

			.verify-logo
				img(src="@/assets/lets-verify-logo.svg", alt="")

			.statistic(v-bind:class="{'start-anim': StatisticAnim}")
				b 5+
					span million

				p Verified Leads 
					br
					| Delivered 
					br
					| to 1,400+ Clients
</template>

<script>
	import { Scrolly, ScrollyViewport, ScrollyBar } from 'vue-scrolly'
	import UserBlock from '@/components/User-Action'
	export default {
		name: 'RightSidebar',
		components:{
			UserBlock,
			Scrolly,
			ScrollyViewport,
			ScrollyBar
		},
		data(){
			return {
				clientsList: false,
				setupLimit: false,
				activeCampaign: '',
				RS: '',
				activeSettingsCount: 0,
				activeTacticsCount: 0,
				
				// animation props
				DatabaseAnim: false,
				StatisticAnim: false
			}
		},
		created: function(){
			// this.runHour();
			setTimeout(() => this.startSidebarAnimation(), 1000)
		},
		// computed: mapGetters(['RightSidebar']);
		computed: {
			RightSidebar(){
				return this.$store.getters.RightSidebar;
			},
			// SelectedCampaign() {
			// 	return this.$store.getters.SelectedCampaign
			// }
			SC(){
				return this.$store.getters.SC.id;
			}
		},
		watch: {
			RightSidebar(){
				this.RS = this.$store.state.RightSidebar;
			},
			SC(){
				// this.RS = this.$store.state.SC;
				this.activeCampaign = this.$store.state.SelectedCampaign;
			},
			// SelectedCampaign(){
			// 	this.activeCampaign = this.$store.state.SelectedCampaign;
			// }
		},
		methods:{
			sortFunc: function (obj){
				return obj.sort((a, b) => a.id - b.id );
			},
			startSidebarAnimation(){
				this.DatabaseAnim = true;
				this.StatisticAnim = true;
			},
			showMore(){
				if (!this.setupLimit) {
					this.setupLimit = 12
				}else{
					this.setupLimit = false;
				}
			}
		}
	}
</script>