<template lang="pug">
	.dropbox(v-bind:class="{opened: open}", v-click-outside="handleFocusOut")
		.visible(v-on:click="open = !open")
			input(type="text", v-bind:value="active", v-on:input="$emit('input', {id:'', name: $event.target.value})", placeholder="Text here...").inp
		.box-list
			span(v-for="item in list", v-on:click="select(item)") {{ item.name }}
</template>

<script>
	export default {
		props: {
			list: [Object, Array],
			default: String
		},
		data() {
			return {
				active: this.default,
				open: false
			}
		},
		methods: {
			select(data){
				this.open = false;
				this.active = data.name;
				// var inpObj = { id: '', name: data.name}
				this.$emit('input', data);
			},
			handleFocusOut() {
				this.open = false;
			}
		}
	}
</script>